.uxMenu-menu {
  position: relative;
}

.uxMenu-menu [class^='animate-'], .uxMenu-menu [class*=' animate-'] {
  visibility: visible;
}

/**
 * Menus contained within Asides
 */
.uxMenu-in-uxAside .uxAside-header a {
  color: rgba(255, 255, 255, 0.6);
  transition: color 300ms;
}

.uxMenu-in-uxAside .uxAside-header a:hover {
  color: #fff;
}

.uxMenu-in-uxAside .uxAside-header .uxMenu-top {
  position: relative;
}

.uxMenu-in-uxAside .uxAside-header .uxMenu-back {
  background: url("data:image/png;base64,") no-repeat 50% 50%;
  animation: none;
}

.uxMenu-in-uxAside .uxAside-header .uxMenu-back:not(.animate-fadeIn) {
  pointer-events: none;
  opacity: 0;
}

.uxMenu-wrap {
  position: relative;
  overflow: hidden;
  width: 100%;
  transition: height 300ms;
}

.uxMenu-level {
  position: absolute;
  top: 0;
  left: 0;
  visibility: hidden;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.uxMenu-level:focus {
  outline: none;
}

.uxMenu-level.uxMenu-level--current {
  visibility: visible;
}

.uxMenu-item {
  display: block;
  width: 100%;
}

.uxMenu-link {
  position: relative;
  display: block;
  transition: color 100ms;
  font-size: 16px;
  line-height: 16px;
}

.uxMenu-link .micon {
  line-height: 16px;
}

.uxMenu-top {
  display: flex;
}

.uxMenu-top a {
  transition: color 300ms;
}

.uxMenu-breadcrumbs {
  flex: auto;
}

.uxMenu-breadcrumbs .uxMenu-breadcrumb {
  display: inline-block;
  cursor: pointer;
  text-transform: uppercase;
  vertical-align: middle;
  padding: 0 6px 0 0;
}

.uxMenu-breadcrumbs .uxMenu-breadcrumb .micon {
  padding: 0 2px 0 0;
}

.uxMenu-breadcrumbs .uxMenu-breadcrumb.animate-fadeIn {
  animation: fadeIn 0.6s;
}

.uxMenu-breadcrumbs .uxMenu-breadcrumb.animate-fadeOut {
  animation: fadeOut 0.6s;
}

.uxMenu-breadcrumbs .uxMenu-breadcrumb:last-child {
  pointer-events: none;
}

.uxMenu-back {
  z-index: 100;
  cursor: pointer;
  text-transform: uppercase;
  opacity: 0;
}

.uxMenu-back.animate-fadeIn {
  animation: fadeIn 0.6s;
  opacity: 1;
}

.uxMenu-back.animate-fadeOut {
  pointer-events: none;
  animation: fadeOut 0.6s;
}

.uxMenu-back:hover, .uxMenu-back:focus {
  outline: none;
}

.uxMenu-back--hidden {
  pointer-events: none;
  animation: fadeOut 0.6s;
}

.animate-outToRight .uxMenu-item {
  animation: outToRight 0.6s both cubic-bezier(0.7, 0, 0.3, 1);
}

.animate-outToLeft .uxMenu-item {
  animation: outToLeft 0.6s both cubic-bezier(0.7, 0, 0.3, 1);
}

.animate-inFromLeft .uxMenu-item {
  animation: inFromLeft 0.6s both cubic-bezier(0.7, 0, 0.3, 1);
}

.animate-inFromRight .uxMenu-item {
  animation: inFromRight 0.6s both cubic-bezier(0.7, 0, 0.3, 1);
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes outToRight {
  to {
    opacity: 0;
    transform: translate3d(100%, 0, 0);
  }
}

@keyframes outToLeft {
  to {
    opacity: 0;
    transform: translate3d(-100%, 0, 0);
  }
}

@keyframes inFromLeft {
  from {
    opacity: 0;
    transform: translate3d(-100%, 0, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes inFromRight {
  from {
    opacity: 0;
    transform: translate3d(100%, 0, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
